<template>
  <div class="container">
    <page-title first-title="账户信息"></page-title>
    <suppliers-base-info :suppliersInfo="suppliers_info" :suppliersId="suppliers_id"></suppliers-base-info>
    <section>
      <h3>{{ statement_info.statement_no }}账单汇总详情</h3>
      <span>结算单编号：</span>
      <el-button type="primary" class="export" @click="exportWindList">导出结算单</el-button>
      <label>{{ statement_info.statement_no }}</label>
      <div>
        <h4 class="title-item">收入</h4>
        <div class="title-item">
          <p>销售总额（元）</p>
          <p>{{ statement_info.total_add }}</p>
        </div>
        <div class="title-item">
          <p>现金收入（元）</p>
          <p>{{ statement_info.add_cash }}</p>
        </div>
        <div class="title-item" v-if="cooperateType == 1">
          <p>积分收入</p>
          <p>{{ statement_info.add_huanbi }}</p>
        </div>
        <div class="title-item">
          <p>笔数</p>
          <p>{{ statement_info.num_add }}</p>
        </div>
      </div>
      <div>
        <h4 class="title-item">支出</h4>
        <div class="title-item">
          <p>退款总额（元）</p>
          <p>-{{ statement_info.total_sub }}</p>
        </div>
        <div class="title-item">
          <p>退款现金（元）</p>
          <p>-{{ statement_info.sub_cash }}</p>
        </div>
        <div class="title-item" v-if="cooperateType == 1">
          <p>退积分&emsp;</p>
          <p>-{{ statement_info.sub_huanbi }}</p>
        </div>
        <div class="title-item">
          <p>笔数</p>
          <p>{{ statement_info.num_sub }}</p>
        </div>
      </div>
      <div>
        <h4 class="title-item">结算</h4>
        <div class="title-item">
          <p>结算总额（元）</p>
          <p>{{ statement_info.total }}</p>
        </div>
        <div class="title-item">
          <p>结算现金（元）</p>
          <p>{{ statement_info.cash }}</p>
        </div>
        <div class="title-item" v-if="cooperateType == 1">
          <p>结算积分</p>
          <p>{{ statement_info.huanbi }}</p>
        </div>
      </div>
    </section>
    <div class="list-wrapper">
      <el-tabs type="border-card" v-model="listParams.type" @tab-click="tabClick">
        <el-tab-pane label="总收入" name="1">
          <el-table
            :data="list"
            style="width: 100%"
            v-loading="isLoading"
            :header-cell-style="{ background: '#eef1f6' }"
          >
            <el-table-column prop="order_sn" label="订单号"></el-table-column>
            <el-table-column prop="add_time" label="下单时间" :formatter="dateFormat"></el-table-column>
            <el-table-column label="实付金额（元）">
              <template slot-scope="scope">
                <span v-if="scope.row.order_type == 1">{{ scope.row.total_amount }}</span>
                <span v-else>{{ scope.row.goods_amount }}</span>
              </template>
            </el-table-column>
            <el-table-column label="支付现金额（元）">
              <template slot-scope="scope">
                <span v-if="scope.row.order_type == 1">{{ scope.row.real_money_paid }}</span>
                <span v-else>{{ scope.row.money_paid }}</span>
              </template>
            </el-table-column>
            <el-table-column label="支付积分额" v-if="cooperateType == 1">
              <template slot-scope="scope">
                <span v-if="scope.row.order_type == 1">{{ scope.row.real_surplus }}</span>
                <span v-else>{{ scope.row.surplus }}</span>
              </template>
            </el-table-column>
            <el-table-column prop="real_cash_back" label="现金退款"></el-table-column>
            <el-table-column prop="real_surplus_back" label="积分退款" v-if="cooperateType == 1"></el-table-column>
            <el-table-column prop="shipping_fee" label="运费"></el-table-column>
            <el-table-column prop="showStatus" label="订单状态"></el-table-column>
            <el-table-column label="操作">
              <template slot-scope="scope">
                <el-button @click="viewDetail(scope.row.order_id)" type="text" size="small">查看</el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>

        <el-tab-pane label="总支出" name="2" v-loading="isLoading">
          <el-table :data="list" style="width: 100%">
            <el-table-column prop="order_sn" label="订单号"></el-table-column>
            <el-table-column prop="add_time" label="下单时间" :formatter="dateFormat"></el-table-column>
            <el-table-column prop="totalBack" label="支出金额（元）"></el-table-column>
            <el-table-column prop="cash_back" label="支出现金额（元）"></el-table-column>
            <el-table-column prop="surplus_back" label="支出积分额" v-if="cooperateType == 1"></el-table-column>
            <el-table-column prop="showStatus" label="订单状态"></el-table-column>
            <el-table-column label="操作">
              <template slot-scope="scope">
                <el-button @click="viewDetail(scope.row.order_id)" type="text" size="small">查看</el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </div>
    <el-pagination
      background
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-size="listParams.size"
      layout="prev, pager, next, jumper"
      :total="total"
    ></el-pagination>
  </div>
</template>

<script>
import { getSuppliersStatementInfo, getSuppliersStatementDetailList, exportList } from '../../api/suppliers'
import { mapState } from 'vuex'
import { saveAs } from 'file-saver'
import SuppliersBaseInfo from '../../components/suppliers/SuppliersBaseInfo'

export default {
  name: 'SuppliersStatementInfo',
  data() {
    return {
      suppliers_statement_id: this.$route.params.id ? this.$route.params.id : '', //账单id
      total: 10,
      currentPage: 1,
      listParams: {
        type: '1',
        suppliers_id: '',
        suppliers_statement_id: this.$route.params.id ? this.$route.params.id : '', //账单id
        size: 10,
        page: 1
      },
      suppliers_info: {
        suppliers_name: '',
        shop_name: '',
        manager_name: '',
        manager_tel: '',
        suppliers_nam: ''
      },
      list: [],
      statement_info: {
        suppliers_statement_id: '', //结算id
        type: 1, //账单类型
        statement_no: '', //结算单号
        addtime: '', //结算生成日期
        total_add: 0, //总收入
        add_cash: 0, //现金收入
        add_huanbi: 0, //积分收入
        num_add: 0, //收入笔数
        total_sub: 0, //总支出
        sub_cash: 0, //现金支出
        sub_huanbi: 0, //积分支出
        num_sub: 0, //支出笔数
        huanbi: 0, //结算积分
        cash: 0, //结算现金
        total: 0, //结算总额
        bill_start_time: '',
        bill_end_time: ''
      },
      isLoading: false //是否正在加载
    }
  },
  components: {
    SuppliersBaseInfo
  },
  created() {
    this.getStatementInfo()
    this.getDetailList()
  },
  computed: {
    ...mapState({
      suppliers_id: state => state.suppliers.suppliers_id,
      suppliers_name: state => state.suppliers.suppliers_name,
      cooperateType: state => state.suppliers.cooperateType
    })
  },
  methods: {
    getStatementInfo() {
      let data = {}
      data['suppliers_statement_id'] = this.suppliers_statement_id
      data['suppliers_id'] = this.suppliers_id
      getSuppliersStatementInfo(data).then(
        res => {
          this.suppliers_info = res.suppliers_info
          this.statement_info = res.statement_info
        },
        error => {
          console.log(error)
        }
      )
    },
    getDetailList() {
      this.isLoading = true
      this.listParams.suppliers_id = this.suppliers_id
      getSuppliersStatementDetailList(this.listParams)
        .then(
          res => {
            this.list = res.list
            this.total = res.paged.total
          },
          error => {
            console.log(error)
            this.list = []
            this.total = 0
          }
        )
        .finally(() => {
          this.isLoading = false
        })
    },
    handleCurrentChange(currentPage) {
      this.listParams.page = currentPage
      this.getDetailList()
    },
    dateFormat: function(row, column) {
      let date = row[column.property]
      return this.utils.formatDate('YYYY-MM-DD HH:mm:ss', date * 1e3)
    },
    viewDetail(id) {
      // let url = window.location.protocol + '//' + window.location.host + window.location.pathname + 'admin'
      window.location.href = '/admin/order.php?act=info&order_id=' + id
    },
    tabClick(val) {
      this.listParams.type = val.name
      this.currentPage = 1
      this.getDetailList()
    },
    exportWindList() {
      let idList = [this.suppliers_statement_id]
      exportList({
        suppliers_id: this.suppliers_id,
        suppliers_statement_id: idList
      }).then(
        res => {
          const date = this.utils.formatDate('YYYY-MM-DD', new Date().getTime())
          saveAs(new Blob([res.data]), res.filename || this.suppliers_name + date + '结算单.xls')
        },
        error => {
          console.log(error)
        }
      )
    }
  }
}
</script>

<style lang="scss" scoped>
section {
  background-color: #fff;
  padding: 15px 10px;
  margin-top: 16px;
  h2 {
    margin: 5px 5px;
  }
  h3 {
    text-align: center;
  }
  label {
    margin-right: 30px;
  }
}
.container {
  font-size: 15px;
  .title-item {
    vertical-align: middle;
    display: inline-block;
    margin: auto 20px;
  }
  .search-wrapper {
    padding: 10px;
    margin: 10px 0;
    background: #fff;
    border: 1px solid #eee;
    .el-form-item {
      margin-bottom: 0;
    }
  }
  .button-wrapper {
    margin-bottom: 10px;
  }
  .list-wrapper {
    margin-top: 16px;
    margin-bottom: 10px;
  }
  .export {
    float: right;
  }
  .el-pagination {
    text-align: center;
  }
}
</style>
